import { mapMutations } from 'vuex'

export default {
	data() {
		return {
			title: 'Hello'
		}
	},
	computed: {},
	onReady () {
		console.log('首页 Ready')
		this.changeLoading(false)
	},
	onLoad () {
		console.log('首页 Load')
		this.changeLoading(true)
	},
	onHide () {
		console.log('首页 Hide')
	},
	onShow () {
		console.log('首页 Show')
		this.changeLoading(false)
	},
	onShareAppMessage (options) {
		console.log(options)
	},
	methods: {
		...mapMutations(['changeLoading'])
	}
}
